@font-face {
  font-family: 'Gilroy';
  src: url('/assets/font/Gilroy-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: url('/assets/font/Gilroy-Extrabold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
}

.G {
  font-family: 'Gilroy', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  word-break: break-word;
}

a {
  text-decoration: none;
}

body {
  background: #fff;
  color: #252525;
  font-family: 'Gilroy', sans-serif;
}

.header {
  background-image: url(/assets/img/header.png);
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: contain;
  background-color: #043054;
  height: 100vh;
  position: relative;
}

.header__title {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 35%;
  margin: auto;
  color: #fff;
  width: fit-content;
  height: fit-content;
}

.header h1 {
  font-size: 78px;
}

.header span {
  color: #FA6700;
}

.header p {
  font-size: 40px;
}

.about {
  background-image: url(/assets/img/about.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 100px 6% 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.about__img {
  max-height: 500px;
  max-width: 80%;
  margin-bottom: 50px;
}

.about__main {
  margin-left: 50px;
}

.about__text {
  max-width: 560px;
  margin-bottom: 50px;
}

.about h2 {
  font-size: 40px;
  color: #252525;
  margin-bottom: 50px;
}

.about p {
  font-size: 18px;
}

.about .experience {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 700px;
  padding-top: 100px;
}

.about .experience__item {
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 300px;
  margin-bottom: 150px;
}

.about .experience__title {
  font-size: 20px;
  color: #252525;
  font-weight: bold;
  margin-bottom: 40px;
}

.about .experience img {
  margin-top: -35%;
}

.about .experience__term {
  font-size: 16px;
  margin-bottom: 50px;
}

.stuck {
  background-image: url(/assets/img/stuck.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.stuck h2 {
  padding-top: 100px;
  text-align: center;
  font-size: 40px;
  margin-bottom: 100px;
  color: #fff;
}

.stuck__main {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 80%;
  margin: auto;
  max-width: 1200px;
}

.stuck__item {
  flex-basis: 250px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 100px;
}

.stuck__item img {
  width: 100%;
  height: 150px;
  margin-bottom: auto;
}

.stuck__item p {
  font-size: 40px;
  color: #FA6700;
}

.contact {
  background-image: url(/assets/img/contact.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.contact h2 {
  font-size: 40px;
  color: #252525;
  text-align: center;
  padding-top: 100px;
  margin-bottom: 100px;
}

.contact form {
  width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contact input {
  width: 44%;
  padding: 15px;
  border-radius: 5px;
  border: 2px solid #d8d8d8;
  margin-bottom: 50px;
}

.contact .message {
  width: 100%;
  margin-bottom: 100px;
  padding: 15px;
  border-radius: 5px;
  border: 2px solid #d8d8d8;
}

.contact button {
  margin: 0 auto 100px;
  color: #252525;
  background-color: #FA6700;
  border: none;
  padding: 5px 15px;
  font-size: 23px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.7s;
}

.contact button:hover {
  color: #fff;
  transform: scale(1.1);
}

.contact .error {
  width: 100%;
  text-align: center;
  margin-top: -50px;
}

@media only screen and (max-width: 1400px) {
  .heading {
    font-size: 48px;
  }
}

@media only screen and (max-width: 1024px) {
  .header {
    height: 50vh;
  }

  .header h1 {
    font-size: 48px;
  }

  .header p {
    font-size: 25px;
  }
}

@media only screen and (max-width: 768px) {
  .about__img {
    display: none;
  }

  .about__main {
    width: 100%;
    margin: 0;
  }

  .about .experience {
    width: 100%;
  }

  .stuck {
    background-size: auto;
    background-color: #021033;
  }

  .stuck__item {
    margin-bottom: 50px;
  }

  .contact input {
    width: 100%;
  }
}

@media only screen and (max-width: 550px) {
  .header {
    height: 25vh;
  }

  .header h1 {
    font-size: 28px;
  }

  .header p {
    font-size: 20px;
  }
}

@media only screen and (max-width: 450px) {
  .about {
    padding-top: 50px;
  }

  .about h2 {
    text-align: center;
  }

  .about .experience {
    justify-content: center;
  }

  .contact h2 {
    padding: 100px 10px 0;
  }
}